
.names input {
    margin-top: 40px;
    height: 20px;
    padding: 10px;
    font-size: 1em;
    border: 1px solid #F3C809;
    border-radius: 5px;
    font-family: 'Nunito Sans', sans-serif;
    
}

.names button {
    margin-left: 10px;
    height: 40px;
    background-color: #F3C809;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 0px 10px;
    cursor: pointer;
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 16px;
    
}

.names button:hover {
    opacity: 0.6;

}